@media only screen and (max-width: 600px) {
    .react-3d-carousel .home-page-images {
        height: 17em;
        padding: 1em 0em;
    }
}
@media only screen and (min-width: 601px) {
    .react-3d-carousel  {
        height: 35em;
    }
    .react-3d-carousel .home-page-images {
        padding: 1em 0em;
        height: 34em;
    }
    
}
